<template>
  <div class="morning-parents-container">
    
    <!-- <img class="banner-back" src="@/assets/images/morning_parents_banner_back.png" alt=""> -->
    <div class="top-block-1" />
    <BaseBanner
      class="morning-parents-banner-box"
      :bannerData="bannerData"
      @clickBanner="clickBanner"
    />

    <div class="split-div-1" />

    <!-- // -- 最近收听 -->
    <div v-if="recentHeard.albumList && recentHeard.albumList.length > 0" class="BaseModularLayout">
      <BaseShowAllRow
        rowTitle="最近收听"
        @click="seeAllRecentHeard"
        class="title-row"
      />
      <div class="layout-item-box">
        <div
          v-for="album in recentHeard.albumList"
          :key="album._id"
          class="layout-item"
        >
          <div @click="clickAlbum(album)">
            <component
              :album="album"
              :is="BaseModularLayout"
            />
          </div>
        </div>
        <div class="split-div-layout" />
      </div>
    </div>

    <div v-if="recentHeard.albumList && recentHeard.albumList.length > 0" class="split-div-2" />

    <!-- // -- 早安父母模块下的所有栏目 -->
    <div v-if="morning.categoryList && morning.categoryList.length > 0">
      <div
        v-for="(everyCategory, categoryIndex) in morning.categoryList"
        :key="everyCategory._id"
        :style="{ 'display': everyCategory.albums.length > 0 ? 'block' : 'none' }"
        :class="[`${getTemplateClassName(everyCategory.template)}`]"
      >
        <BaseShowAllRow
          :rowTitle="everyCategory.name"
          @click="seeAllClassifyAlbums(everyCategory._id, everyCategory.name)"
          class="title-row"
        />
        <div class="layout-item-box">
          <div
            v-for="(album, index) in everyCategory.albums"
            :key="album._id"
            class="layout-item"
          >
            <div @click="clickAlbum(album)">
              <component
                :album="album"
                :is="getTemplateNameImport(everyCategory.template)"
              />
            </div>
            <div v-if="index < everyCategory.albums.length - 1" class="split-line-1" />
          </div>
        </div>
        
        <div v-if="categoryIndex < morning.categoryList.length - 1" class="split-div-2" />
      </div>
    </div>

    <div v-if="morning.categoryList && morning.categoryList.length > 0" class="split-div-2" />

    <!-- // ! 精选推荐模块 -->
    <!-- <div v-if="command.albumList && command.albumList.length > 0" class="list-layout">
      <BaseShowAllRow
        rowTitle="精选推荐"
        @click="seeAllClassifyAlbums(command._id, command.title)"
        class="title-row"
      />
      <div
        v-for="album in recentHeard.albumList"
        :key="album._id"
        class="layout-item-box"
      >
        <div @click="clickAlbum(album)" class="layout-item">
          <component
            :is="BaseListLayout"
            :album="album"
          />
        </div>
        <div class="split-div-layout" />
      </div>
    </div> -->

    <div v-if="command.albumList && command.albumList.length > 0" class="split-div-2" />
    
    <!-- // ! 我的购买 -->
    <div v-if="myBuy && myBuy.albumList.length > 0" class="BaseModularLayout">
      <BaseShowAllRow
        rowTitle="我的购买"
        @click="seeAllMyBought"
        class="title-row"
      />
      <div class="layout-item-box">
        <div
          v-for="album in myBuy.albumList"
          :key="album._id"
          class="layout-item"
        >
          <div @click="clickAlbum(album)">
            <component
              :is="BaseModularLayout"
              :album="album"
            />
          </div>
        </div>
      </div>
    </div>

    <div class="split-div-2" />

    <Tabbar class="tabbar-fixed-bottom" />

    <div class="bottom-block-135" />
  </div>
</template>

<script>
import BaseBanner from '@/components/banner/BaseBanner.vue';
import Tabbar from '@/components/tabbar/Tabbar.vue';
import BaseShowAllRow from '@/components/showAllRow/BaseShowAllRow.vue';
import { apiBanner } from '@/service/api/banner';
import { apiCategoryList, apiCategoryRecommendList, apiAlbumBuylist, apiAlbumPlayList, ApiAlbumListByClassifyid, apiAlbumInfo } from '@/service/api/program';
import { MORNING_PARENTS } from '@/utils/category';
import { toAlbumPage } from '@/utils/proxyRouteSkip';
import { getTemplateName } from '@/utils/constant';
import Vue from 'vue';
import { Button, Toast } from 'vant';

Vue.use(Button).use(Toast);

export default {
  components: {
    BaseBanner,
    Tabbar,
    BaseShowAllRow,
  },

  data() {
    return {
      // 动态组件 样式在('@/assets/style/tabbarPage/homeLayout.scss')
      BaseNewsLayout: (resolve) => require(['@/components/homePageLayout/BaseNewsLayout.vue'], resolve),
      BaseListLayout: (resolve) => require(['@/components/homePageLayout/BaseListLayout.vue'], resolve),
      BaseModularLayout: (resolve) => require(['@/components/homePageLayout/BaseModularLayout.vue'], resolve),

      bannerData: null,
      
      morning: {
        categoryList: [],
        pageNum: 0,
        pageSize: 20
      },

      command: {
        categoryList: [],
        pageNum: 0,
        pageSize: 20
      },

      recentHeard: {
        albumList: [],
        pageNum: 0,
        pageSize: 3
      },

      myBuy: {
        albumList: [],
        pageNum: 0,
        pageSize: 3
      },

      generalClassify: {
        pageNum: 0,
        pageSize: 20
      }
    }
  },

  created() {
    document.title = '青豆父母学堂';
  },

  async mounted() {
    this.getBannerData();
    this.getCategoryList();
    this.getRecentHeard();
    // this.getCommandList();
    this.getMyBuyList();
  },

  methods: {
    // todo 点击 banner
    async clickBanner(albumId) {
      const albumInfoResponse = await apiAlbumInfo(albumId);
      if (!albumInfoResponse || albumInfoResponse.type === 'fail') return Toast('服务器错误，请联系客服');
      const albumInfo = albumInfoResponse.data;
      toAlbumPage(albumInfo.chargeType, albumInfo.isBuy, albumInfo._id);
    },

    async getMyBuyList() {
      const myBuyResponse = await apiAlbumBuylist(this.myBuy.pageNum, this.myBuy.pageSize);
      if (myBuyResponse.type === 'fail') return Toast( '服务器错误，请联系客服' );
      this.myBuy.albumList = myBuyResponse.data;
    },

    async getRecentHeard() {
      const recentHeardResponse = await apiAlbumPlayList(this.recentHeard.pageNum, this.recentHeard.pageSize);
      if (recentHeardResponse.type === 'fail') return Toast( '服务器错误，请联系客服' );
      this.recentHeard.albumList = recentHeardResponse.data;
    },

    // ? 编译不同模板的样式文件
    getTemplateClassName(templateId) {
      return getTemplateName( templateId );
    },

    // ? 编译不同模板
    getTemplateNameImport(templateId) {
      return this[ getTemplateName( templateId ) ];
    },

    async getAlbumLists() {
      const allClassifyAlbums = [];
      for (let i = 0, len = this.morning.categoryList.length; i < len; i++) {
        if (this.morning.categoryList[i].isDisplay === false) continue;
        allClassifyAlbums.push( this.getAlbumList( this.morning.categoryList[i]._id, 0, this.morning.categoryList[i].displayAlbumCount ) );
      }

      const allClassifyResults = await Promise.all( allClassifyAlbums );

      // 把各个栏目的 albums 放到 morning.categoryList 中
      for (let i = 0, len = this.morning.categoryList.length; i < len; i++) {
        if (this.morning.categoryList[i].isDisplay === false) continue;
        if (allClassifyResults[i].type === 'fail') continue;
        if (allClassifyResults[i].data.length === 0) {
          this.morning.categoryList.splice(i, 1);
          continue;
        }
        this.morning.categoryList[i].albums = allClassifyResults[i].data;
      }
    },

    async getAlbumList( classifyId, pageNum = this.generalClassify.pageNum, pageSize = this.generalClassify.pageSize ) {
      return ApiAlbumListByClassifyid( classifyId, pageNum, pageSize);
    },

    async getCommandList() {
      const commandResponse = await apiCategoryRecommendList( MORNING_PARENTS, this.command.pageNum, this.command.pageSize );
      if (!commandResponse || commandResponse.type === 'fail') return Toast( '服务器错误，请联系客服' );
      this.command.albumList = commandResponse.data;
    },

    async getCategoryList() {
      const categoryResponse = await apiCategoryList( MORNING_PARENTS, this.morning.pageNum, this.morning.pageSize );
      if (!categoryResponse || categoryResponse.type === 'fail') return Toast( '服务器错误，请联系客服' );
      this.morning.categoryList = categoryResponse.data;
      
      this.getAlbumLists();
    },

    async getBannerData() {
      const bannerResponse = await apiBanner( MORNING_PARENTS );
      if (!bannerResponse || bannerResponse.type === 'fail') return Toast( '服务器错误, 请联系客服人员' );
      this.bannerData = bannerResponse.data;
    },
    
    // todo 跳转至我的收听历史页面
    seeAllRecentHeard() {
      this.$router.push({
        name: 'my-listen-history',
        query: {
          docTitle: encodeURIComponent( '最近收听' )
        }
      });
    },
    
    // todo 跳转至全部专辑页面
    seeAllClassifyAlbums(classifyId, classifyTitle) {
      this.$router.push({
        name: 'total-album',
        query: {
          classifyId,
          classifyTitle: encodeURIComponent( classifyTitle )
        }
      });
    },

    // todo 跳转至我的购买
    seeAllMyBought() {
      this.$router.push({
        name: 'my-bought',
      });
    },

    // todo 跳转至专辑介绍页
    clickAlbum(album) {
      toAlbumPage(album.chargeType, album.isBuy, album._id);
    }
  },
}
</script>

<style lang="scss" scoped>
.morning-parents-container {
  width: 100%;
  background: #fff;

  .banner-back {
    position: absolute;
    top: 0;
  }
  .top-block-1 {
    width: 100%;
    height: 30px;
  }
  .morning-parents-banner-box {
    box-sizing: border-box;
    padding: 0 16px;
  }

  .split-div-1 {
    width: 100%;
    height: 1px;
    background: rgba(238,238,238,1);
    margin-top: 23px;
  }

  .split-div-2 {
    width: 100%;
    height:10px;
    background:rgba(246,246,246,1);
  }
}
</style>


